@media screen and (-webkit-min-device-pixel-ratio:0)
  and (min-resolution:.001dpcm) {
    .Option-Container{display:flex;} 
}


.Option-Container {
  display: block ruby;
  cursor: pointer;
}
.array-barContainer {
  position: relative;
  width: 100%;
  text-align: center;
  height: 800px;
}

.array-bar {
  position: relative;
  /* width: 1.88%; */
  background-color: turquoise;
  display: inline-block;
  margin: 0 1px;
  overflow: auto;
  justify-content: center;
}
.TimerDisplay {
  display: flex;
  float: right;
  color: rgb(187, 187, 187);
  padding-bottom: 5%;
}

#RightLayout {
  float: right;
}
select {
  background-color: rgb(24, 24, 24);
  color: rgb(187, 187, 187);
  border: none;
  border-radius: 2%;
  font: 20px solid;
  margin-left: 15px;
}
option {
  background-color: white;
}
#Heading {
  font-size: 23px;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  background-color: rgb(59, 59, 59);
  border: none;
  border-radius: 9%;
  padding: 10px;
  margin: 20px;
  color: rgb(173, 173, 173);
  display: inline-block;
}
#Algo-Parent {
  display: none;
  position: absolute;
  background-color: rgba(65, 65, 66, 0.98);
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 1;
}

#Algo-Parent button,
#ColorParent button {
  padding: 12px 16px;
  text-decoration: none;
  display: block;
}
#Heading:hover #Algo-Parent {
  display: block;
}

#Algo-Parent button:hover {
  cursor: pointer;
  background-color: rgb(122, 122, 122);
}
#Analysis-Parent{
  position: relative;
  width: 50%;
  height: 60vh;
  transform: translate(50%, -50%);
  z-index: 1;
}
.Analysis-Tools {
 
  background-color: rgb(22, 0, 48);
  position: absolute;
  border-radius: 10px;
  top:50%;
  left: 50%;
  transform: translate(-50%,-50%);
}
.Analysis-Tools #Title {
  display: flex;
  height: 30px;
  background-color: rgb(43, 8, 83);
  border-radius: 10px 10px 0 0;
}
#Title div {
  text-align: center;
  font-size: 25px;
  background-color: rgb(43, 8, 83);
  width: 100%;
  border-radius: 10px;
}
#Title button {
  position: relative;
  padding: 1px;
  top: -20px;
  height: 100%;
}
#Body {
  padding: 15px;
  text-align: center;
  font-size: 20px;
  background-color: inherit;
  position: relative;
}
#body header {
  font-size: 30px;
}
#Body div section {
  text-align: left;
  position: relative;
  height: 200%;
  background: rgb(24, 24, 24);
  width: fit-content;
  padding: 30px;
}
.r {
  display: grid;
  grid-auto-flow: column;
  grid-template-columns: auto auto auto auto;
}
.r div {
  border: 1px solid;
  display: grid;
  grid-auto-flow: column;
  height: 40px;
  width: 180px;
  overflow: hidden;
}
