body {
  background-color: black;
  color: white;
}
button {
  font-size: 17px;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  background-color: rgb(59, 59, 59);
  border: none;
  border-radius: 9%;
  padding: 10px;
  margin: 20px;
  color: rgb(173, 173, 173);
  cursor: pointer;
}
input {
  font-size: 16px;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  background-color: rgb(59, 59, 59);
  border: none;
  padding: 10px;
  margin: 20px;
  color: rgb(173, 173, 173);
}
